@tailwind base;
@tailwind components;
@tailwind utilities;


html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.site-colorr{
  background-color: rgb(11, 65, 56);
}

.site-texxt{
  color: rgb(11, 65, 56);
}

.land-height{
  height: 100vh;
}

.landing-shape{
  height: 100%;
  background-color: rgb(11, 65, 56);
  position: relative;
  clip-path: polygon(0% 0%, 100% 0%, 100% 35%, 30% 100%, 0% 80%); /*rgb(67, 68, 68)*/
  
  /*border-radius: 0px 0px 1000px 400px;borderBottomLeftRadius: "50%";
  borderBottomRightRadius: "50%";*/
}

.triangle {
  width: 70%;
  height: 50%;
  clip-path: polygon(0% 100%, 100% 0%, 100% 100%);
  background-color: white;
  position: absolute; /* Positioning the triangle */
  bottom: 0; /* Align to the bottom */
  right: 0; /* Align to the right */
}

.trapezium {
  clip-path: polygon(0% 30%, 100% 0%, 100% 100%, 0% 100%);
}


.complement {
  clip-path: polygon(20% 0%, 100% 0%, 100% 100%, 0% 100%);
}

.h-96pro {
  height: 400px;
}

.project-category {
  transition: transform 0.3s ease-in-out, opacity 0.3s ease-in-out;
}
.open-project {
  transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out;
}
.downboss:hover .project-category {
  transform: translateY(-20px);
  opacity: 0;
}
.downboss:hover .open-project {
  opacity: 1;
  transform: translateY(0);
}


@media screen and (min-width: 1024px) {
  .almost-full{
    width: 400px;
    height: 400px;
  }
}

/* Styles for extra-large screens (â‰¥ 1280px) */
@media screen and (min-width: 1280px) {
  .almost-full{
    width: 500px;
    height: 500px;
  }
}